<script setup>
import { ref, reactive, defineProps } from "vue";
import { required, helpers } from "@vuelidate/validators";
import { useVuelidate } from "@vuelidate/core";
import * as CommonFunctions from "../../common/ValidationResponseFormatter";

const submitted = ref(false);

const data = reactive({
  Username: "",
  Password: "",
});

const checkUsername = (value) => {
  var regexVal = /[A-Za-z]+$/;
  if (regexVal.test(value)) {
    return true;
  } else {
    return false;
  }
};

const rules = {
  Username: {
    required: helpers.withMessage("Username is required", required),
    checkUsername: helpers.withMessage("Username is invalid", checkUsername),
  },
  Password: { required: helpers.withMessage("Password is required", required) },
};

const v$ = useVuelidate(rules, data);

const props = defineProps({
  onSubmitClick: {
    type: Function,
  },
});

const submit = async (isFormValid) => {
  submitted.value = true;
  if (!isFormValid) {
    return;
  }

  let obj = JSON.parse(JSON.stringify(data));
  event.preventDefault();
  props.onSubmitClick(obj);
  clearForm();
};

const clearForm = () => {
  for (const key in data) {
    data[key] = "";
  }

  submitted.value = false;
};
</script>

<template>
  <div>
    <form @submit.prevent="submit(!v$.$invalid)">
      <div>
        <label for="Username" class="block text-900 font-medium mb-2"
          >Username</label
        >
        <InputText
          id="Username"
          type="text"
          placeholder="Username"
          :class="[
            {
              'p-invalid': v$.Username.$invalid && submitted,
              'w-full mb-2': true,
            },
          ]"
          v-model="v$.Username.$model"
        />
        <small v-if="v$.Username.$invalid && submitted" class="p-error">{{
          CommonFunctions.ValidationResponseFormatter(v$.Username)
        }}</small>
        <br />
        <br />
        <label for="password" class="block text-900 font-medium mb-2"
          >Password</label
        >
        <Password
          id="password"
          type="password"
          placeholder="Password"
          :class="[
            {
              'p-invalid': v$.Password.$invalid && submitted,
              'w-full mb-2': true,
            },
          ]"
          v-model="v$.Password.$model"
          toggleMask
          :feedback="false"
        />

        <small v-if="v$.Password.$invalid && submitted" class="p-error">{{
          CommonFunctions.ValidationResponseFormatter(v$.Password)
        }}</small>
        <br />
        <br />
        <Button
          label="Login"
          icon="pi pi-user"
          class="w-full p-3"
          type="submit"
        ></Button>
      </div>
    </form>
  </div>
</template>
